body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol, ul {
  padding-left: 30px;
}

.game {
}

.points {
  margin-bottom: 10px;
  width: 100px;

}

.points-left {
  float:left;
  width: 50%;
}

.points-right {
  float:right;
}

.points::after {
  clear:both;
  content: "";
  display: table;
}

.game-info {
  padding-left: 20px;
  padding-bottom: 5px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #000;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.line-0 {
  background: -webkit-linear-gradient(bottom,
                              transparent 0%,
                              calc(50% - 2px),
                              black calc(50% - 2px),
                              black calc(50% + 2px),
                              calc(50% + 2px),
                              transparent 100%);
  background: -o-linear-gradient(bottom,
                              transparent 0%,
                              calc(50% - 2px),
                              black calc(50% - 2px),
                              black calc(50% + 2px),
                              calc(50% + 2px),
                              transparent 100%);
  background: linear-gradient(0deg,
                              transparent 0%,
                              calc(50% - 2px),
                              black calc(50% - 2px),
                              black calc(50% + 2px),
                              calc(50% + 2px),
                              transparent 100%);
}

.line-45 {
  background: -webkit-linear-gradient(45deg,
                              transparent 0%,
                              calc(50% - 2px),
                              black calc(50% - 2px),
                              black calc(50% + 2px),
                              calc(50% + 2px),
                              transparent 100%);
  background: -o-linear-gradient(45deg,
                              transparent 0%,
                              calc(50% - 2px),
                              black calc(50% - 2px),
                              black calc(50% + 2px),
                              calc(50% + 2px),
                              transparent 100%);
  background: linear-gradient(45deg,
                              transparent 0%,
                              calc(50% - 2px),
                              black calc(50% - 2px),
                              black calc(50% + 2px),
                              calc(50% + 2px),
                              transparent 100%);
}

.line-90 {
  background: -webkit-linear-gradient(left,
  	                          transparent 0%,
                              calc(50% - 2px),
                              black calc(50% - 2px),
                              black calc(50% + 2px),
                              calc(50% + 2px),
  	                          transparent 100%);
  background: -o-linear-gradient(left,
  	                          transparent 0%,
                              calc(50% - 2px),
                              black calc(50% - 2px),
                              black calc(50% + 2px),
                              calc(50% + 2px),
  	                          transparent 100%);
  background: linear-gradient(90deg,
  	                          transparent 0%,
                              calc(50% - 2px),
                              black calc(50% - 2px),
                              black calc(50% + 2px),
                              calc(50% + 2px),
  	                          transparent 100%);
}

.line-135 {
  background: -webkit-linear-gradient(315deg,
  	                          transparent 0%,
                              calc(50% - 2px),
                              black calc(50% - 2px),
                              black calc(50% + 2px),
                              calc(50% + 2px),
  	                          transparent 100%);
  background: -o-linear-gradient(315deg,
  	                          transparent 0%,
                              calc(50% - 2px),
                              black calc(50% - 2px),
                              black calc(50% + 2px),
                              calc(50% + 2px),
  	                          transparent 100%);
  background: linear-gradient(135deg,
  	                          transparent 0%,
                              calc(50% - 2px),
                              black calc(50% - 2px),
                              black calc(50% + 2px),
                              calc(50% + 2px),
  	                          transparent 100%);
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game-board {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.reset {
  float: left;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  width: 100px;
  margin-top: 5px;
  padding: 0;
  text-align: center;
}



